body{
  font-family:"Montserrat", sans-serif;
  margin:0;
  padding:0;
  color:white;
}

mas {
  background-color: black;
  height:200px;
}

.lista {
  display:flex;
  list-style: none;
  align-content: flex-end;
  font-weight:500;
  letter-spacing: 2px;
  
  margin:0;
  
}
.lista li{
  padding:25px 15px;
  margin-right:5px;
  transition: background-color 0.3s ease-in 10ms;
}

.lista li:hover{
  background-color:rgba(0, 0, 0, 0.2);
}

.lista li:last-child{
  margin-right:25px;
}
.navbar {
  background-image:linear-gradient(163deg, #4964da 17%, rgba(146, 73, 221, 0.8) 29.6%, rgba(176, 60, 196, 0.6) 42.2%, rgba(192, 55, 173, 0.4) 54.8%, rgba(217, 46, 116, 0) 80%), radial-gradient(100% 100% at 10% 100%,rgba(146, 73, 221, 0.8) 0%,rgba(192, 55, 173, 0.4)20%, #cb61e8 30%, #da49e8 44.29%, #db44d7 68.57%, #da3fc8 82.86%, #df1286 100%);;
  background-size:400% 400%;
  margin:0;
  padding:0;
  flex-direction: row-reverse;
  display:flex;
  box-shadow: 2px 2px 10px  rgba(0,0,0,0.3);
  animation: myAnimation 10s ease infinite;
}
@keyframes myAnimation{
  0% {
  background-position: 0% 50%;
}
50% {
  background-position: 100% 50%;
}
100% {
  background-position: 0% 50%;
}
}

.navbar:hover {
  animation-play-state:paused;
}
